/**
 * Zemit Module Initialization
 * @author: <contact@dannycoulombe.com>
 * Creation date: 2018-09-24
 * 
 * Initialize all the modules
 */
(function() {
	
	Zemit.modules = {};
	
	var moduleList = [];
	Zemit.module = (name, configs = {}, args = {}) => {
		
		configs.priority = configs.priority === undefined ? 0 : configs.priority;
		configs.dependencies = configs.dependencies === undefined ? [] : configs.dependencies;
		configs.group = configs.group === undefined ? 'misc' : configs.group;
		
		moduleList.push([name, configs, args]);
	};
	
	Zemit.app.config(['$ocLazyLoadProvider', function($ocLazyLoadProvider) {
		
		$ocLazyLoadProvider.config({
			// debug: Zemit.version === 'dev',
			events: true
		});
	}]);
	
	Zemit.app.run(['$hook', '$modules', '$util', '$injector', '$debug', '$i18n', '$sidebar', '$session', ($hook, $modules, $util, $injector, $debug, $i18n, $sidebar, $session) => {
		
		$hook.add('onReady', () => $debug.init('module', $i18n.get('modules.debugTitle')));
		
		$hook.add('onAllModulesLoaded', () => {
			
			// Update module function to execute directly onAllModulesLoaded
			Zemit.module = (name, configs = {}, args = {}) => {
				
				let callbackParams = args.slice(0, args.length - 1);
				let callbackFunc = args.slice(args.length - 1, args.length)[0];
				let injectors = [];
				callbackParams.forEach((param) => {
					injectors.push($injector.get(param));
				});
				let props = callbackFunc.apply(null, injectors);
				props.priority = configs.priority || 0;
				props.dependencies = configs.dependencies || [];
				props.group = configs.group || 'misc';
				
				$modules.config(name, props.group, props);
			};
			
			// Initialize previously added modules
			if(moduleList.length > 0) {
				
				moduleList.forEach(module => {
					let props = module[1];
					module[1].dependencies = $modules.getLowestPriority(props.priority, props.dependencies);
				})
				moduleList.sort((a, b) => {
					return a[1].priority - b[1].priority;
				});
				moduleList.forEach(module => {
					let dependencies = module[1].dependencies;
					for(let i = 0; i < dependencies.length; i++) {
						if(!$modules.isActivated(dependencies[i])) {
							$modules.addModule(module[0], module[1].group, module[1]);
							return;
						}
					}
					
					Zemit.module.apply(null, module);
				});
			}
			
			$hook.run('onAllModulesRan');
		});
		
		let settings = $session.get('settings');
		$sidebar.addTab('modules', {
			title: $i18n.get('core.components.sidebar.tabs.modules'),
			directive: 'zm-sidebar-modules',
			isVisible: () => {
				return settings.context === 'structure';
			}
		})
	}])
	
	Zemit.app.factory('$modules', ['$ocLazyLoad', '$session', '$hook', '$i18n', '$util', '$injector', '$rootScope', '$debug', '$sidebar', '$toolbar', function($ocLazyLoad, $session, $hook, $i18n, $util, $injector, $rs, $debug, $sidebar, $toolbar) {
		
		var settings = $session.get('settings');
		$session.prepare('settings', {
			modules: {}
		});
		
		var factory = {
			
			items: {},
			
			bootstrap: (modules = []) => {
				
				if(modules instanceof String) {
					modules = [modules];
				}
				
				var moduleLoadCount = 0;
				modules.forEach((name) => {
					
					$debug.log('module', 'INIT', name.toUpperCase());
					
					if(Zemit.version === 'dev') {
						
						// Initialize a new module
						Zemit.modules[name] = angular.module('zm' + $util.camelize(name, true), []);
						
						// Lazy-load the newly created module
						$ocLazyLoad.load({
							name: name,
							rerun: true,
							reconfig: true,
							files: [
								'./modules/' + name + '/' + name + '.js',
								'./modules/' + name + '/' + name + '.css'
							]
						}).then(response => {
							
							moduleLoadCount++;
							
							if(moduleLoadCount === modules.length) {
								
								$hook.add('onBeforeReady', () => {
									$hook.run('onAllModulesLoaded');
								});
								
								if($hook.hasRan('onBeforeReady')) {
									$hook.run('onAllModulesLoaded');
								}
							}
						});
					}
					else {
						
						moduleLoadCount++;
						
						if(moduleLoadCount === modules.length) {
							
							$hook.add('onBeforeReady', () => {
								$hook.run('onAllModulesLoaded');
							});
							
							if($hook.hasRan('onBeforeReady')) {
								$hook.run('onAllModulesLoaded');
							}
						}
					}
				});
			},
			
			addModule: function(name, group = 'misc', props = {}, isCore = false) {
				
				let options = {
					modules: {}
				};
				options.modules[name] = {
					activated: false
				};
				$session.prepare('settings', options);
				
				let defaultThumbnail = 'data:image/png;base64,';
				
				// Defaut props
				props = Object.assign({
					toolbar: {
						left: [],
						middle: [],
						right: []
					},
					sidebar: {
						tabs: [],
						icons: []
					}
				}, props);
				
				let settingsDefault = {};
				settingsDefault[name] = props.defaultSettings;
				$session.prepare('settings', {
					modules: settingsDefault
				});
				
				let module = {
					_activated: settings.modules[name].activated,
					_isCore: isCore,
					
					isCore: function() {
						return this._isCore;
					},
					isActivated: function() {
						return this._activated;
					},
					activate: function() {
						this._activated = true;
					},
					deactivate: function() {
						this._activated = false;
					},
					
					name: name,
					priority: props.priority || 0,
					dependencies: props.dependencies || [],
					title: $i18n.get((isCore ? 'core.' : '') + 'modules.' + group + '.' + name + '.title'),
					desc: $i18n.get((isCore ? 'core.' : '') + 'modules.' + group + '.' + name + '.desc'),
					group: group,
					props: props || {},
					thumbnail: defaultThumbnail,
					settings: settings.modules[name]
				}
				
				let thumbnail = './' + (isCore ? 'core/' : '') + 'modules/' + name + '/' + name + '.png';
				let image = new Image();
				image.src = thumbnail;
				image.onload = () => {
					module.thumbnail = thumbnail;
					$rs.$digest();
				};
				
				if(!this.items[group]) {
					this.items[group] = {
						name: group,
						title: $i18n.get('core.modules.' + group),
						modules: []
					};
				}
				
				this.items[group].modules.push(module);
				
				return module;
			},
			
			config: function(name, group = 'misc', props = {}, isCore = false) {
				
				let module = this.addModule(name, group, props, isCore);
				
				if(props.onConfig instanceof Function) {
					props.onConfig(module);
				}
				if(module.isActivated()) {
					this.register(module);
				}
				
				return module;
			},
			
			register: function(module) {
				
				module.activate();
				
				// Initialize directives
				for(let directive in module.props.directives) {
					Zemit.app.compileProvider.directive(directive, () => {
						return module.props.directives[directive]
					});
				}
				
				// Initialize factories
				for(let factory in module.props.factories) {
					Zemit.app.provide.factory(factory, module.props.factories[factory]);
				}
				
				// Initialize modules
				if(module.props.onInit instanceof Function) {
					module.props.onInit($injector);
				}
				
				// Implement sidebar tabs
				if(module.props.sidebar.tabs instanceof Array) {
					module.props.sidebar.tabs.forEach(options => {
						$sidebar.addTab(module.name, options)
					});
				}
				
				// Implement sidebar icons
				if(module.props.sidebar.icons instanceof Array) {
					module.props.sidebar.icons.forEach(options => {
						$sidebar.addIcon(module.name, options)
					});
				}
				
				// Implement toolbar regions
				if(module.props.toolbar instanceof Object) {
					let keys = Object.keys(module.props.toolbar);
					keys.forEach(region => {
						let items = module.props.toolbar[region];
						items.forEach(item => {
							$toolbar.addItem(region, module.name, item);
						});
					});
				}
			},
			
			get: function(name) {
				
				let keys = Object.keys(this.items);
				for(let i = 0; i < keys.length; i++) {
					
					let modules = this.items[keys[i]].modules;
					for(let y = 0; y < modules.length; y++) {
						if(modules[y].name === name) {
							return modules[y];
						}
					}
				}
				
				return null;
			},
			
			getLowestPriority: function(priority, dependencies) {
				
				for(var i = 0; i < dependencies.length; i++) {
					let module = this.get(dependencies[i]);
					if(module && module.priority >= priority) {
						priority = module.priority + 1;
					}
				}
				
				return priority;
			},
			
			dependenciesAllActivated: function(module) {
				
				for(var i = 0; i < module.dependencies.length; i++) {
					if(!this.isActivated(module.dependencies[i])) {
						return false;
					}
				}
				
				return true;
			},
			
			isActivated: function(name) {
				
				let module = this.get(name);
				return module && module.isActivated();
			}
		};
		
		return factory;
	}]);
})();